<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <RecommendTitle>推荐歌单</RecommendTitle>
    <ul class="row">
    <PlayListCard v-for="item in personalized.slice(0,6)" :key="item.id" :item="item" class="card"/>
    </ul>
     <RecommendTitle>最新音乐</RecommendTitle>
     <ul>
       <Newest v-for="item in  newsongs" :key="item.id" :item="item" @change-current-play-song="
          $emit('change-current-play-song', $event);
          $emit('change-current-play-song-list', newsongs);
        "/>
     </ul>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import RecommendTitle from '@/components/RecommendTitle.vue'
import PlayListCard from '@/components/PlayListCard.vue'
import Newest from '@/components/Newest.vue'
export default {
  name: 'Recommend',
   data:function(){
     return{
      personalized:[],
      newsongs:[]
     }

   },
  components: {
    // HelloWorld
    RecommendTitle,
    PlayListCard,
    Newest
  },
  created(){
    this.axios.get('https://music.kele8.cn/personalized')
  .then( (response)=> {
    console.log(response.data.result);
    this.personalized=response.data.result
  })
  .catch(function (error) {
    console.log(error);
  });
  this.axios.get('https://music.kele8.cn/personalized/newsong')
  .then( (response)=> {
    console.log(response.data.result);
    this.newsongs=response.data.result
  })
  .catch(function (error) {
    console.log(error);
  });
  }
}
</script>
<style scoped lang="less">
   .row{
     display: flex;
     flex-wrap: wrap;
     .card{
       flex: 1 0 30%;
       &:nth-child(3n+2){
         margin: 0 5px;
       }
     }
   }
</style>