<template>
  <li
    class="current-play-list-item"
    @click="$emit('change-current-play-song', item)"
  >
    <div
      class="playing-animate"
      v-if="currentPlaySong.id == item.id"
      :class="{ paused: !playing }"
    >
      <span></span>
      <span></span>
      <span></span>
    </div>
    <div
      class="detail"
    >
      <span>{{ item.name }} </span>-
      <span class="artist" v-for="artist in item.song ? item.song.artists : item.ar" :key="artist.id">
        {{ artist.name }}
      </span>
    </div>
    <div class="delete" @click="$emit('remove-song',item.id)"></div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
    currentPlaySong: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less">
.current-play-list-item {
  margin: 0 15px;
  display: flex;
  justify-content: space-between;
  @keyframes shensuo {
    from {
      height: 20%;
    }
    to {
      height: 100%;
    }
  }
  .playing-animate {
    width: 15px;
    height: 15px;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    margin-right: 5px;
    span {
      width: 15%;
      height: 20%;
      background: red;
      animation: shensuo 0.6s linear infinite alternate;
      &:nth-child(1) {
        animation-delay: 0s;
      }
      &:nth-child(2) {
        animation-delay: -0.2s;
      }
      &:nth-child(3) {
        animation-delay: -0.4s;
      }
    }
    &.paused {
      span {
        animation-play-state: paused;
      }
    }
  }
  .detail {
    margin-bottom: 10px;
    flex: 1;
    font-size: 16px;
    .artist {
      font-size: 10px;
      color: #999;

      &::after {
        content: "/";
      }
      &:last-of-type {
        &::after {
          content: "";
        }
      }
    }
  }
  .delete {
    background: url(../assets/2.png);
    width: 20px;
    height: 20px;
    background-size: 20px 20px;
  }
}
</style>