<template>
  <section class="lyric">
    <!-- <ul :style="{ marginTop: -currentLyricIndex * 21 + 'px' }"> -->
      <div @click.stop="$emit('chu-show')" style="width:110px;height:100px"></div>
    <ul
      ref="lyricBox"
      :style="{ marginTop: -offsetTop + 'px' }"
      v-if="parsedLyric"
    >
      <li
        v-for="(item, index) in parsedLyric"
        :key="index"
        :class="{ active: index == currentLyricIndex, paused: !playing }"
      >
        <span
          :style="{
            animationDuration:
              index >= parsedLyric.length - 1
                ? (duration - item.time) / 2 + 's'
                : parsedLyric[index + 1].time - item.time + 's',
          }"
          >{{ item.text }}</span
        >
      </li>
    </ul>
    
  </section>
</template>

<script>
export default {
  props: {
    parsedLyric: Array,
    currentLyricIndex: Number,
    playing: Boolean,
    duration: Number,
  },
  data: function () {
    return {
      offsetTop: 0,
    };
  },
  mounted() {},
  watch: {
    currentLyricIndex: function () {
      var h = 0;
      this.$refs.lyricBox.querySelectorAll("li").forEach((element, index) => {
        if (index < this.currentLyricIndex) {
          h += element.offsetHeight;
        }
      });
      this.offsetTop = h;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes mymove {
  from {
    background-position-x: -100%;
  }
  to {
    background-position-x: -200%;
  }
}
.lyric {
  overflow: scroll;
  ul {
    margin: 0 30px;
    transition: 0.3s all;
    // transform: translateY(-100px);
    li {
      margin-bottom: 10px;
      text-align: center;
      &.active {
        font-weight: bold;
        span {
          background: linear-gradient(to right, #eb1717 50%, #fff 55%);
          background-clip: text;
          -webkit-text-fill-color: transparent;
          background-size: 200%;
          background-position-x: -100%;
          animation: mymove linear;
        }
      }
      &.paused{
        span{
          animation-play-state: paused;
        }
      }
    }
  }
}
</style>