<template>
  <section>
    <div class="list-header">
      <div class="text">
        <div
          class="mask"
          :style="{
            backgroundImage: `url(
        ${detail.coverImgUrl}?imageView=1&type=webp&thumbnail=120x0
      )`,
          }"
        ></div>
        <div class="plhead_fl lsthd_fl">
        <img
          :src="`${detail.coverImgUrl}?imageView=1&type=webp&thumbnail=236x0`"
          class="fall"
          alt=""
        />
        <span class="lsthd_icon">歌单</span>
        <i class="icon">{{ detail.playCount | parsePalyCount }}</i>
        </div>
        <div class="right">
          <span class="name">{{ detail.name }}</span>
          <div style="margin-top: 20px; border-radius: 50%">
            <img
              :src="`${detail.coverImgUrl}?imageView=1&type=webp&thumbnail=30x0`"
              alt=""
              style="border-radius: 50%"
            />
          </div>
        </div>
      </div>
    </div>
    <p>歌曲列表</p>
    <ul v-if="detail">
      <Newest
        @change-current-play-song="
          $emit('change-current-play-song', $event);
          $emit('change-current-play-song-list', detail.tracks);
        "
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        :index="index"
      />
    </ul>
    <p>最新评论</p>
    <ul>
      <CommentCard
        v-for="item in comments"
        :key="item.commentId"
        :item="item"
      />
    </ul>
  </section>
</template>

<script>
import Newest from "../components/Newest";
import CommentCard from "../components/CommentCard";
export default {
  components: {
    Newest,
    CommentCard,
  },
  data: function () {
    return {
      detail: Object,
      comments: null,
    };
  },
  created() {
    this.axios
      .get("https://music.kele8.cn/playlist/detail?id=" + this.$route.query.id)
      .then((res) => {
        console.log(res.data.playlist);
        this.detail = res.data.playlist;
      });
    this.axios
      .get("https://music.kele8.cn/comment/playlist", {
        params: {
          id: this.$route.query.id,
        },
      })
      .then((res) => {
        this.comments = res.data.hotComments;
      });
  },
  filters: {
    parsePalyCount: function (value) {
      return value >= 10000
        ? parseFloat((value / 10000).toFixed(1)) + "万"
        : value;
    },
  },
};
</script>

<style lang="less" scoped>
p {
  height: 20px;
  line-height: 20px;
  background: rgb(223, 228, 228);
  font-size: 10px;
  padding-left: 20px;
}
.list-header {
  position: relative;
  width: 100%;
  height: 180px;
  background: url(${item.coverImgUrl}) rgba(0, 0, 0, 0.25);
  color: aliceblue;
  .text {
    position: absolute;
    top: 30px;
    left: 20px;
    display: flex;
    .mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 90%;
      height: 100%;
      background-size: cover;
      background-position: center;
      filter: blur(7px) brightness(0.9);
      transform: scale(1.5);
      z-index: -1;
    }
    .plhead_fl{
      width: 50%;
      height: 100%;
    position: relative;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 18px;
        z-index: 2;
        background-color: rgba(0, 0, 0, 0.2);
      }
     .lsthd_icon {
      position: absolute;
      z-index: 3;
      top: 10px;
      left: 0;
      padding: 0 8px;
      height: 17px;
      color: #fff;
      font-size: 9px;
      text-align: center;
      line-height: 17px;
      background-color: rgba(217, 48, 48, 0.8);
      border-top-right-radius: 17px;
      border-bottom-right-radius: 17px;
    }
    i {
      position: absolute;
      top: 5px;
      right: 0;
      color: white;
      font-size: 12px;
      line-height: 1em;
      padding: 0 10px;
      &::before {
        content: "";
        width:12px;
        height: 11px;
        display: inline-block;
        background: url("data:image/svg+xml;base64,");
        background-repeat: no-repeat;
        vertical-align: bottom;
        margin-right: 4px;
      }
    }
    }
  
  }
}
.right {
  margin-left: 20px;
  font-size: 18px;
}
</style>