<template>
  <div id="app" :class="{ showbar: currentPlaySong }">
    <ul id="nav" v-if="!$route.meta.hideNav">
      <li><router-link to="/">推荐音乐</router-link></li>
      <li><router-link to="/hot">热歌榜</router-link></li>
      <li><router-link to="/search">搜索</router-link></li>
    </ul>
    <Transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut "
    >
      <router-view
        @change-current-play-song="changeCurrentPlaySong"
        @change-current-play-song-list="currentPlaySongList = $event"
      />
    </Transition>
    <Play
      v-if="currentPlaySong"
      :currentPlaySong="currentPlaySong"
      :currentPlaySongList="currentPlaySongList"
      @change-current-play-song="changeCurrentPlaySong"
      @prev-song="prevSong"
      @next-song="nextSong"
      @remove-song="removeSong"
    />
  </div>
</template>
<script>
import Play from "@/components/Play.vue";

export default {
  components: {
    Play,
  },
  data: function () {
    return {
      currentPlaySong: null,
      currentPlaySongList: [],
    };
  },
  computed: {
    currentSongIndex: function () {
      return this.currentPlaySongList.findIndex(
        (e) => e.id === this.currentPlaySong.id
      );
    },
  },
  methods: {
    changeCurrentPlaySong: function (payload) {
      this.currentPlaySong = payload;
    },
    prevSong: function () {
      this.currentPlaySong = this.currentPlaySongList[
        this.currentSongIndex <=0 
          ? this.currentPlaySongList.length - 1
          : this.currentSongIndex - 1
      ];
    },
    nextSong: function () {
      this.currentPlaySong = this.currentPlaySongList[
        this.currentSongIndex >= this.currentPlaySongList.length - 1
          ? 0
          : this.currentSongIndex + 1
      ];
    },
    removeSong:function(p){
      this.currentPlaySongList=this.currentPlaySongList.filter(item =>
        item.id !== p
      )

    }
  },
};
</script>

