<template>
  <li class="newest-item" @click="$emit('change-current-play-song', item)">
    <div class="index" v-if="index !== undefined">
      {{ (index + 1).toString().padStart(2, 0) }}
    </div>
    <div class="info">
      <div class="detail">
        <h3>{{ item.name }}</h3>
        <p>
          <span
            class="artist"
            v-for="artist in item.song ? item.song.artists : item.ar"
            :key="artist.id"
          >
            {{ artist.name }}
          </span>

          <i class="album">
            - {{ item.song ? item.song.album.name : item.al.name }}</i
          >
        </p>
      </div>
      <div class="icon"></div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
    index: Number,
  },
};
</script>

<style lang="less">
li.newest-item {
  display: flex;
  align-items: center;
 
  .index {
    margin-left: 10px;
  }
  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(3) {
    .index {
      color: red;
    }
  }
  .info{
    flex: 1;
    display: flex;
   padding: 10px 0;
  border-bottom: 1px solid lightgray;
  padding-right: 10px;
  margin-left: 10px;
     .detail {
    flex: 1;
    h3 {
    }
    p {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      font-size: 12px;
      color: #888;
      .artist {
        &::after {
          content: "/";
        }
      }
      span {
        &:last-of-type {
          &::after {
            content: "";
          }
        }
      }
      i {
      }
    }
  }
  }
  .icon {
    width: 22px;
    height: 22px;
    background-image: url("../assets/index_icon_2x.png");
    background-size: 166px 97px;
    background-position: -24px 0;
  }
}
</style>