<template>
  <section class="disc" :class="{paused: !playing}" @click="$emit('hide-show')">
    <img
      :src="`${
        currentPlaySong.picUrl || currentPlaySong.al.picUrl
      }?imageView=1&type=webp&thumbnail=246x0`"
      alt=""
    />
  </section>
</template>

<script>
export default {
  props: {
    currentPlaySong: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less">
@keyframes disc-rotate {
  from{
    transform: rotate(0deg);
  }
  to{
    transform: rotate(360deg);
  }
}
.disc{
  margin: 150px 100px ;
  img{
    border-radius: 50%;
    animation: disc-rotate 10s linear infinite;
  }
  &.paused{
    img{
      animation-play-state: paused;
    }
  }
}
</style>