<template>
  <section class="playbar">
    <img
      :src="`${
        currentPlaySong.picUrl || currentPlaySong.al.picUrl
      }?imageView=1&type=webp&thumbnail=60x0`"
      alt=""
    />
    <h5>{{ currentPlaySong.name }}</h5>
    <div class="progress">
      <span
        v-if="!playing"
        class="play-icon"
        @click.stop="$emit('audio-play')"
      ></span>
      <span
        v-else
        class="paused-icon"
        @click.stop="$emit('audio-pause')"
      ></span>
      <canvas ref="canvas" width="40" height="40"></canvas>
    </div>
    <div class="currentlist" @click.stop="$emit('show-list')"></div>
  </section>
</template>

<script>
export default {
  props: {
    currentPlaySong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
  },
  computed: {
    circlePercent: function () {
      return this.duration ? this.currentTime / this.duration : 0;
    },
  },
  methods: {
    drawCircle: function (n) {
      var canvas = this.$refs.canvas;
      const ctx = canvas.getContext("2d");

      ctx.clearRect(0, 0, 40, 40);
      // ctx.fillRect(0, 0, 40, 40);

      // ctx.strokeStyle = "#ddd";

      // ctx.beginPath();
      // ctx.arc(20, 20, 18, 0, 2 * Math.PI);
      // // ctx.closePath();
      // ctx.stroke();

      ctx.strokeStyle = "#dd001b";

      ctx.beginPath();
      ctx.arc(20, 20, 15, -0.5 * Math.PI, (2 * n - 0.5) * Math.PI);
      ctx.stroke();

      ctx.strokeStyle = "#ccc";

      ctx.beginPath();

      ctx.arc(20, 20, 15, -0.5 * Math.PI, (2 * n - 0.5) * Math.PI, true);
      ctx.stroke();
    },
  },
  mounted() {
    this.drawCircle(this.circlePercent);
  },
  watch: {
    circlePercent: function (n) {
      // console.log(n);
      this.drawCircle(n);
    },
  },
};
</script>

<style lang="less">
.playbar {
  width: 100%;
  height: 50px;
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  padding: 5px 10px;
  align-items: center;
  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.2);
  img {
    border-radius: 50%;
    width: 40px;
    height: 40px;
  }
  h5 {
    flex: 1;
    padding: 0 10px;
    font-size: 14px;
  }
  .progress {
    position: relative;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      &.play-icon {
        width: 10px;
        height: 10px;
        overflow: hidden;
        &::before {
          content: "";
          width: 20px;
          height: 10px;
          display: block;
          border-top: 5px solid transparent;
          border-right: 12px solid transparent;
          border-bottom: 5px solid transparent;
          border-left: 8px solid #bbb;
          margin-left: 2px;
        }
      }
      &.paused-icon {
        width: 8px;
        height: 8px;
        overflow: hidden;
        &::before {
          content: "";
          width: 100%;
          height: 100%;
          display: block;
          background: #bbb;
        }
      }
    }

    canvas {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
    }
  }
  .currentlist {
    background: url("../assets/leibiao.png") no-repeat;
    background-size: 100%;
    width: 20px;
    height: 20px;
    margin-left: 15px;
  }
}
</style>